.Navbar {
    position: fixed;
    width: 100%;
    background-color: rgba(27, 38, 44, 0.486);
    box-sizing: 5px 5px 7px rgba(0, 0, 0, 0.219);
    padding: 1.5rem 0;
    z-index: 50;
}

.Navbar-nav {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}

.Navbar-nav a {
    color: #ffffff;
    text-decoration: none;
    display: inline-block;
}

.Navbar-nav a::after {
    display: block;
    content: "";
    border-bottom: 2px solid #ffffff;
    width: 100%;
    height: 5px;
    transform: scaleX(0);
    transform-origin: center;
    transition: all 0.3s;
}

.Navbar-nav #Navbar-Logo a::after {
    border: none;
}

.Navbar-nav a:hover::after {
    transform: scaleX(1);
}

.Navbar-Logo {
    font-family: 'Merienda', cursive;
    font-size: 2rem;
    color: blanchedalmond;
    margin: 0 2rem;
}

.dropdown button,
.dropdown button:focus,
.dropdown button:hover,
.dropdown button:active {
    background-color: transparent !important;
    border: none !important;
    outline: none !important;
}

.dropdown {
    margin-right: auto;
    display: flex;
    justify-content: center;
    align-items: center;
}

.home {
    margin-left: auto;
    margin-right: 1rem;
    padding: 0.5rem 0.75rem;
}

.signUp {
    margin: 0 2rem;
}

.dropdown a {
    color: #698474;
}

.dropdown a::after {
    color: #698474;
    border-bottom: 2px solid #698474;
    display: block;
    content: "";
    width: 100%;
    height: 5px;
    transform: scaleX(0);
    transform-origin: center;
    transition: all 0.3s;
}

.dropdown a:hover::after {
    transform: scaleX(1);
}

.activeLink {
    color: #db3056 !important;
    font-weight: 700 !important;
}

.navbar-inside {
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.navbar-inside > *:not(:last-child) {
    margin-right: 0.5rem !important;
}

.dropdown {
}

.dropdown button {
    margin-right: 0.8rem;
}

/* Media Quries */

@media screen and (max-width: 770px) {
    .Navbar-nav {
        flex-flow: column;
    }

    .home {
        margin: 0;
    }

    .dropdown {
        margin: 0;
    }
}